<script setup lang="ts">
import { EnvelopeIcon, PhoneIcon } from '@heroicons/vue/24/outline'

import { BookingUpdatePassengerCount } from '@/features/booking/update-passenger-count'
import { BookingUpdateStatus } from '@/features/booking/update-booking-status'

import type { BookingStatus, ExtraBooking, TourBooking } from '@/shared/types'
import { sanitizeTel } from '@/shared/lib/utils'
import { breakpoints } from '@/shared/lib/style'
import { IconClipboardError, IconClipboardOk } from '@/shared/ui/icons'

export interface Props {
  tourBookingId: TourBooking['id']
  tourCode: string
  startTime: string
  mainPassengerName?: string
  mainPassengerEmail?: string | null
  mainPassengerPhone?: string | null
  tourBookingCode: string
  tourBookingUrl: string | null
  waiverStatus: boolean | null
  status: BookingStatus
  totalCount: number
  arrivedCount: number | null
  extraBookings: ExtraBooking[]
}

defineProps<Props>()

const { smaller } = breakpoints
const isSmall = smaller('sm')
</script>

<template>
  <div class="items-center gap-y-5 bg-white  px-4 py-6 @2xl:px-10 @2xl:py-6">
    <div
      class="
      grid grid-flow-col grid-cols-2 grid-rows-2 @2xl:grid-flow-row
      @2xl:grid-cols-6 @2xl:grid-rows-none
      "
    >
      <div class="flex flex-col gap-0.5 @2xl:col-span-2">
        <p class="text-xs text-gray-700">
          {{ tourCode }}: {{ startTime }}
        </p>

        <p
          class="truncate font-medium"
          v-text="mainPassengerName"
        />

        <div class="flex gap-2.5">
          <a
            v-if="mainPassengerEmail"
            class="inline-block transition-transform hover:scale-125"
            :href="`mailto:${mainPassengerEmail}`"
          >
            <EnvelopeIcon class="h-4 w-4 text-blue-500" />
          </a>

          <a
            v-if="mainPassengerPhone"
            class="inline-block transition-transform hover:scale-125"
            :href="`tel:${sanitizeTel(mainPassengerPhone)}`"
          >
            <PhoneIcon class="h-4 w-4 text-blue-500" />
          </a>
        </div>
      </div>

      <div class="flex max-w-full flex-col gap-1.5 text-xs">
        <component
          :is="tourBookingUrl ? 'a' : 'span'"
          class="truncate font-medium text-blue-500"
          :class="tourBookingUrl && 'hover:underline'"
          :href="tourBookingUrl"
          target="_blank"
          v-text="tourBookingCode"
        />

        <div v-if="waiverStatus !== null" class="flex items-center gap-1.5">
          <span class="text-gray-500">Waivers:</span>

          <IconClipboardOk
            v-if="waiverStatus"
            class="fill-green-600/60"
          />
          <IconClipboardError
            v-else-if="waiverStatus === false"
            class="fill-yellow-500"
          />
        </div>
      </div>

      <div class="flex justify-end">
        <BookingUpdatePassengerCount
          :tour-booking-id="tourBookingId"
          :arrived-count="arrivedCount"
          :total-count="totalCount"
        />
      </div>

      <div class="col-span-2 flex justify-end">
        <BookingUpdateStatus
          :tour-booking-id="tourBookingId"
          :status="status"
          :arrived-count="arrivedCount"
          :total-count="totalCount"
          :hide-label="isSmall"
        />
      </div>
    </div>
    <div v-if="extraBookings.length" class="mt-5 w-full text-sm text-gray-500">
      <div v-for="extraBooking in extraBookings" :key="extraBooking.id">
        <strong>{{ extraBooking.quantity }} &times; {{ extraBooking.extra?.name }}</strong>
        <p v-for="question in extraBooking.questions" :key="question.id" class="pl-5">
          {{ question.label }} {{ question.answer }}
        </p>
      </div>
    </div>
  </div>
</template>
